@import 'myAntd.css';
.m-login-wrap{display: flex; position: absolute;top: 0;left: 0;right: 0;bottom: 0;background: url(../images/m-login-bg.jpg) no-repeat center;}
.m-login{margin: auto;padding: 26px 16px 0;width: 400px;height: 239px;background: url(../images/m-loginform-bg.png) 100% 100%;}
.m-login-row{display: flex; margin: 20px;}
.m-login-icon{width: 40px;font-size: 26px;color: #555;}
.m-login-btn-wrap{margin: 20px; padding: 0 0 0 36px;}
.m-loading{display: none; position: absolute;top: 0;left: 0;right: 0;bottom: 0;background: rgba(0, 0, 0, 0.2);}
.m-loading::before{margin: auto; content: '';display: inline-block;width: 38px;height: 38px;background-image: url(../images/loading.png);animation: loading 0.5s linear infinite;}
.m-loading.active{display: flex;}
.m-index-wrap{display: flex; position: absolute;top: 0;left: 0;right: 0;bottom: 0;}
.m-list{display: flex;flex-direction: column; position: relative;flex:1}
.m-sidebar-icon{display: inline-block; margin: 1px 3px 0; font-size: 18px;vertical-align: top;}
.m-logo{height: 64px;line-height: 64px;text-align: center;font-size: 16px;font-weight: bold; color: white;background-color: red;}
.m-sidebar{display: none;}
.m-sidebar.active{display: block;}
.m-list-header{display: flex; height: 64px;line-height: 64px;border-bottom: 1px solid #ddd;}
.m-list-header-icon{display: inline-block; width: 52px;height: 64px;font-size: 30px;cursor: pointer;text-align: center;}
.m-list-header-icon.rotate{transform: rotate(180deg);}
.m-list-header-icon:hover{background: #ddd;}
.m-list-info{display: flex;flex-direction: column; flex: 1;overflow-y: auto;}
.m-list-header-info{flex: 1}
.m-list-header-menu{width: 200px;}
.m-list-header-menu-welcome{margin: 0 20px 0 0;}
.m-list-header-menu-username{margin: 0 5px;cursor: pointer;}
.m-list-header-menu-theme{margin: 0 0 0 20px;cursor: pointer;}
.m-list-nav{padding: 9px 0 0 20px; height: 40px;border-bottom: 1px solid #ddd;}
.m-empty-page{flex:1;line-height: 300px; text-align: center;color: #ddd;font-size: 50px;overflow-y: auto;background-color: #f0f2f5;}
.m-login-footer{position: absolute;bottom: 0;left: 0;right: 0; line-height: 70px;text-align: center;border-top: 1px solid #e8e8e8;color: white; font-size: 12px; background-color: #426374;}
.m-content-wrap{display: flex;flex-direction: column; flex:1;overflow-y: auto;background-color: #f0f2f5;}
.m-table-wrap{margin: 10px;flex: 1;}
.m-company-img{width: 60px;height: 60px;}
.m-company-check-status{display: inline-block;width: 60px;background: #3bb4f2;text-align: center;border-radius: 4px;color: white;font-weight: bold;font-size: 12px;line-height: 18px;}
.m-action{text-align: center;}
.m-action-btn{margin: 1px 2px; font-size: 12;}
.m-content-search{margin: 10px;padding: 10px; background: white;}
.m-content-table-tool{padding: 10px;background: white;}
.m-btn{margin: 0 5px;}
.m-content-table-tool-row{margin: 5px 0;}







@keyframes loading {
  0% {transform: rotate(0deg);}
  100% { transform: rotate(360deg);}
}